  background-size: cover;
}

#root {
  height: 100%;
}

h1, h2, h3, h4, h5, label, input, p {
  font-family: Playfair Display, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
label,
input,
p {
  color: black;
}

h1 {
  font-weight: 900;
}

