/* Backgrounds */

.btn-light {
  --tw-bg-opacity: 1;
  background-color: rgba(245, 238, 233, var(--tw-bg-opacity));
  padding: 0.5rem;
  padding: 0.5rem;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgba(81, 78, 78, var(--tw-text-opacity));
}

.bg-light {
  background: linear-gradient(180deg, #f3ece7 0%, #faf8f8 100%);
}

.bg-gradient-better {
  background: linear-gradient(180deg, #fff7f1 0%, #e6e2e0 100%);
}

.bg-icons {
  background: linear-gradient(
    180deg,
    rgba(222, 162, 119, 0.76) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}

.shadow-blur {
  background: #c4c4c4;
  box-shadow: 4px 4px 20px #edd8ca;
}

.newShadow {
  box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.25);
}

.page-container {
  height: 100vh;
  background: url(/static/media/landing-bg.6513f494.png) no-repeat center center fixed;
  background-size: cover;
}

.text-shadow {
  mix-blend-mode: normal;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.reg-input {
  background: #f5eee9;
  box-shadow: inset 2px 4.80349px 6px #eedccf;
  border-radius: 4.80349px;
}

.profile-bg {
  background: linear-gradient(
    180deg,
    rgba(245, 238, 233, 0.99) 0%,
    rgba(255, 246, 239, 0.994583) 81.25%,
    #f5eee9 100%
  );
}

.profile-username {
  background: -webkit-linear-gradient(rgb(95, 86, 86), rgb(129, 104, 21));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.profile-section {
  mix-blend-mode: normal;
  border: 1px solid rgba(81, 78, 78, 0.2);
  box-sizing: border-box;
  -webkit-filter: drop-shadow(4px 8px 20px #edd8ca);
  filter: drop-shadow(4px 8px 20px #edd8ca);
}

.input-field {
  background: #f5eee9;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  box-shadow: inset 2px 4.80349px 6px #eedccf;
  border-radius: 4.80349px;
}

