@tailwind base;
@tailwind components;
@tailwind utilities;
@tailwind screens;

/* Resets */
html {
  font-size: 10px;
  background: url('./assets/landing-bg.png') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

#root {
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
label,
input,
p {
  @apply font-serif;
  color: black;
}

h1 {
  @apply font-black md:text-5xl;
}
h2 {
  @apply font-black md:text-4xl;
}
h3 {
  @apply font-bold md:text-3xl;
}
h4 {
  @apply font-normal md:text-2xl;
}
p {
  @apply font-normal md:text-base;
}

/* Backgrounds */
.btn-light {
  @apply p-2 bg-bg text-secondary_100 text-base font-bold p-2;
}

.bg-light {
  background: linear-gradient(180deg, #f3ece7 0%, #faf8f8 100%);
}
.bg-gradient-better {
  background: linear-gradient(180deg, #fff7f1 0%, #e6e2e0 100%);
}
.bg-icons {
  background: linear-gradient(
    180deg,
    rgba(222, 162, 119, 0.76) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}
.shadow-blur {
  background: #c4c4c4;
  box-shadow: 4px 4px 20px #edd8ca;
}
.newShadow {
  box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.25);
}
.page-container {
  @apply h-screen;
  background: url('./assets/landing-bg.png') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.text-shadow {
  mix-blend-mode: normal;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.reg-input {
  background: #f5eee9;
  box-shadow: inset 2px 4.80349px 6px #eedccf;
  border-radius: 4.80349px;
}

.profile-bg {
  background: linear-gradient(
    180deg,
    rgba(245, 238, 233, 0.99) 0%,
    rgba(255, 246, 239, 0.994583) 81.25%,
    #f5eee9 100%
  );
}

.profile-username {
  background: -webkit-linear-gradient(rgb(95, 86, 86), rgb(129, 104, 21));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.profile-section {
  mix-blend-mode: normal;
  border: 1px solid rgba(81, 78, 78, 0.2);
  box-sizing: border-box;
  filter: drop-shadow(4px 8px 20px #edd8ca);
}

.input-field {
  background: #f5eee9;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  box-shadow: inset 2px 4.80349px 6px #eedccf;
  border-radius: 4.80349px;
}

@media only screen and (min-width: 786px) {
  html {
  }
}

.ei {
  max-height: 200px;
  height: auto;
}

.ml-66 {
  margin-left: 100%;
  transform: translate(-110%);
}

button {
  align-items: center;
}

.horzCardFeed {
  max-width: 1127px;
  max-height: 290px;
  width: 80%;
}
.imgFeed {
  max-height: 220px;
}

.whiteCardsFeed {
  max-width: 80%;
}

.profile-cards {
  max-height: 400px;
  max-width: 600px;
}

.profile-horizontalCard {
  max-width: 1000px;
}

.h-90 {
  height: 90%;
}

.input-max-height {
  max-height: 68px;
}

.navbar-max-height {
  max-height: 300px;
}

.landing-max-dimension {
  max-height: auto;
}

@media only screen and (min-width: 1100px) {
  .landing-max-dimension {
    max-height: 600px;
  }
}

.link-fit {
  width: fit-content;
  height: fit-content;
}

:root {
  -moz-transition: width 1s ease-in-out, left 1.5s ease-in-out;
  -webkit-transition: width 1s ease-in-out, left 1.5s ease-in-out;
  -moz-transition: width 1s ease-in-out, left 1.5s ease-in-out;
  -o-transition: width 1s ease-in-out, left 1.5s ease-in-out;
  transition: width 1s ease-in-out, left 1.5s ease-in-out !important;
}

.articleView-img {
  max-height: 600px;
  max-width: 600px;
}
